import React from 'react';
import './index.scss';
import { MdEdit, MdDeleteForever, MdFileUpload } from 'react-icons/md';
import { FaClone } from 'react-icons/fa';

class HoverContainer extends React.Component {
  state = {};
  render() {
    return (
      <div className="hover-container">
        {this.props.children}
        {!this.props.disableHover && (
          <div className="hover-content-container">
            {this.props.isUploadIconShow && (
              <div
                className="content-upload"
                onClick={this.props.uploadHandler}
              >
                <MdFileUpload />
              </div>
            )}
            {this.props.isEditIconShow && (
              <div
                className="edit"
                className="content-edit"
                onClick={this.props.editHandler}
              >
                <MdEdit />{' '}
              </div>
            )}
            {this.props.isDeleteIconShow && (
              <div
                className="content-delete"
                onClick={this.props.deleteHandler}
              >
                <MdDeleteForever />
              </div>
            )}

            {this.props.isCloneIconShow && (
              <div className="content-clone" onClick={this.props.cloneHandler}>
                <FaClone />
              </div>
            )}
          </div>
        )}
      </div>
    );
  }
}

HoverContainer.defaultProps = {
  editHandler: () => {},
  deleteHandler: () => {},
  uploadHandler: () => {},
  cloneHandler: () => {},
  isUploadIconShow: false,
  isEditIconShow: true,
  isDeleteIconShow: true,
  isCloneIconShow: false,
  disableHover: false,
};

export default HoverContainer;
