import React, { useEffect, useState } from 'react';
import { SlideDown } from 'react-slidedown';
import 'react-slidedown/lib/slidedown.css';

const Slide = ({
  open = false,
  content = null,
  header = null,
  sliderStatus,
  customToggler = { open: () => null, close: () => null },
  styleForIcon = { flex: 0.1, justifyContent: 'right' },
  className = '',
}) => {
  const [isOpen, setOpen] = useState(open);

  const handleToggler = e => {
    setOpen(!isOpen);
    sliderStatus(!isOpen, e);
  };

  console.log(content, 'contentdata');

  return (
    <div
      className={`slider ${isOpen ? 'active-slide' : ''}`}
      style={{ cursor: 'pointer', position: 'relative' }}
    >
      <div
        className="slider-header"
        onClick={handleToggler}
        style={{ display: 'flex' }}
      >
        <div style={{ flex: 1 }}>{header}</div>
        <div style={styleForIcon}>
          {isOpen ? customToggler.open : customToggler.close}
        </div>
      </div>
      <SlideDown className={`my-dropdown-slidedown ${className}`}>
        {isOpen ? content : null}
      </SlideDown>
    </div>
  );
};

Slide.defaultProps = {
  sliderStatus: () => {},
};

export default Slide;
